import { Type } from "class-transformer";
import { IsEnum, IsNumber, IsString } from "class-validator";
import { Environment } from './env.enum';
export class EnvironmentVariables {
  @IsEnum(Environment)
  NODE_ENV: Environment;

  @IsString()
  DB_TYPE: string;

  @IsString()
  DB_HOST: string;

  @IsNumber()
  @Type(() => Number)
  DB_PORT: number;

  @IsString()
  DB_USERNAME: string;

  @IsString()
  DB_PASSWORD: string;

  @IsString()
  DB_NAME: string;

  @IsString()
  JWT_SECRET: string;

  @IsString()
  NAT_SERVERS: string;

  @IsString()
  TABLE_PREFIX: string;

  @IsString()
  PASSWORD_SALT: string;

  @IsNumber()
  MICROSERVICE_TIMEOUT: number;

  @IsString()
  MIGRATIONS_TABLE_NAME: string;
}